<template>
  <validation-observer v-slot="{ handleSubmit }">
      <validation-provider name="E-mail" rules="required|email" v-slot="{ errors }">
          <label for="email" class="d-block">Email</label>
          <input v-model="form.email" type="email">
          <span class="d-block">{{ errors[0] }}</span>
      </validation-provider>
      <validation-provider name="Username" rules="min:8|max:12" v-slot="{ errors }">
          <label for="Username" class="d-block">Username</label>
          <input v-model="form.username" type="text">
          <span class="d-block">{{ errors[0] }}</span>
      </validation-provider>
      <validation-provider name="Password" rules="required|min:8" vid="password" v-slot="{ errors }">
          <label for="Password" class="d-block">Password</label>
          <input v-model="form.password" type="text">
          <span class="d-block">{{ errors[0] }}</span>
      </validation-provider>

      <validation-provider name="Password custom" rules="password" v-slot="{ errors }">
          <label for="Password" class="d-block">Password custom</label>
          <input v-model="form.password_custom" type="text">
          <span class="d-block">{{ errors[0] }}</span>
      </validation-provider>

      <button @click="handleSubmit(submit)">Submit</button>
  </validation-observer>
</template>

<script>
export default {
  data() {
      return {
          form: {
              email: '',
              username: '',
              password: '',
              password_custom: ''
          }
      }
  },
  methods: {
      submit() {
          alert('Form has been submitted!');
      }
  }
}
</script>

<style scoped>
  .d-block {
      display: block;
  }
</style>